import "./styles.css";
import RiveCanvas from "@rive-app/canvas-advanced-single";

async function main() {
  const rive = await RiveCanvas();

  const riveFileBytes = await (
    await fetch(new Request("arrow_js.riv"))
  ).arrayBuffer();

  const riveFile = await rive.load(new Uint8Array(riveFileBytes));

  // Create the Rive renderer
  const canvas = document.querySelector("canvas");
  const renderer = rive.makeRenderer(canvas);

  // Mouse Position
  const mouse = {
    x: null,
    y: null
  };

  // Canvas position in the document
  let rect = canvas.getBoundingClientRect();

  const arrowArtboard = riveFile.artboardByName("Arrow");
  const arrowStateMachine = new rive.StateMachineInstance(
    arrowArtboard.stateMachineByName("Arrow"),
    arrowArtboard
  );

  const arrow = arrowArtboard.node("Arrow");

  // On Mouse Move
  document.addEventListener("mousemove", function (event) {
    rotateArrow(event);
  });

  function rotateArrow(event) {
    rect = canvas.getBoundingClientRect();
    // Calculate the position
    mouse.x = event.x - rect.x - 250;
    mouse.y = event.y - rect.y - 250;

    arrow.rotation = Math.atan2(mouse.y, mouse.x);
  }

  // Render loop //
  let lastTime = 0;

  function renderLoop(time) {
    if (!lastTime) {
      lastTime = time;
    }
    const elapsedTimeInMS = time - lastTime;
    const elapsedTimeInSeconds = elapsedTimeInMS / 1000;
    lastTime = time;

    // Clear the canvas
    renderer.clear();

    arrowStateMachine.advance(elapsedTimeInSeconds);
    arrowArtboard.advance(elapsedTimeInSeconds);

    renderer.save();

    arrowArtboard.draw(renderer);
    renderer.restore();

    rive.requestAnimationFrame(renderLoop);
  }
  rive.requestAnimationFrame(renderLoop);
}

main();
